<template>
    <div id="Add">
        <h1 class="page-header">添加用户</h1>
        <form action="" v-on:submit="addCustomer">
            <div class="well">
                <h4>用户信息</h4>
                <div class="form-group">
                    <label for="">姓名</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.name">
                </div>
                <div class="form-group">
                    <label for="">性别</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.sex">
                </div>
                <div class="form-group">
                    <label for="">邮箱</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.email">
                </div>
                <div class="form-group">
                    <label for="">学历</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.education">
                </div>
                <div class="form-group">
                    <label for="">毕业学校</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.graduationschool">
                </div>
                <div class="form-group">
                    <label for="">职业</label>
                    <input type="text" name="" id="" class="form-control" placeholder="" v-model="customer.profession">
                </div>
                <div class="form-group">
                    <label for="">个人简介</label>
                    <textarea name="" id="" cols="30" rows="10" class="form-control" v-model="customer.profile"></textarea>
                </div>
                <button type="submit" class="btn btn-primary" >添加</button>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    name:"Add",
    data(){
        return {
            customer:{}
        }
    },
    methods:{
        addCustomer(e){
            if(!this.customer.name || !this.customer.sex || !this.customer.email){
                console.log("none!")

            }
            else
            {
                let newCustomer = {
                    name:this.customer.name,
                    sex:this.customer.sex,
                    email:this.customer.email,
                    education:this.customer.education,
                    graduationschool:this.customer.graduationschool,
                    profession:this.customer.profession,
                    profile:this.customer.profile

                };
                this.$http.post("http://localhost:3000/users",newCustomer)
                .then((data)=>{
                    this.$router.push({path:"/",query:{alert:"用户信息添加成功！"}})
                })
                e.preventDefault();

            }
            e.preventDefault();
        }
        
    }
}
</script>
