<template>
    <div id="CustomerDetails">
        <h1 class="page-header">{{customer.name}}</h1>
        <ul class="list-group">
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.sex}}</span></li>
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.email}}</span></li>
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.education}}</span></li>
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.graduationschool}}</span></li>
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.profession}}</span></li>
            <li class="list-group-item"><span class="el-icon-platform-eleme">{{customer.profile}}</span></li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "CustomerDetails",
        data(){
            return {
                customer:""
            }
        },
        created() {
            this.fetchCustomers(this.$route.params.id);

        },
        methods:{
            fetchCustomers(id){
                this.$http.get("http://localhost:3000/users/"+id)
                    .then((data)=>{
                        this.customer=data.body;
                    });

            }
        },
    }
</script>

