<template>
    <div id="Customers container">
        <Alert v-if="AlertData" v-bind:message="AlertData"></Alert>
        <h1 class="page-header">用户管理系统</h1>
        <table class="table tabletable-striped">
            <thead>
                <tr>
                    <th>姓名</th>
                    <th>性别</th>
                    <th>邮箱</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="customer in customers" :key=customer.id>
                    <td>{{customer.name}}</td>
                    <td>{{customer.sex}}</td>
                    <td>{{customer.email}}</td>
                    <td><router-link class="btn btn-default" v-bind:to="'/customer/'+customer.id">详情</router-link></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    import Alert from './Alert'
export default {
    name:"Customers",
    components:{
        Alert
    },
    data(){
        return {
            customers:[],
            AlertData:""
        }
    },
    created(){

        this.fetchCustomers();
        this.$http.get("/api")
            .then((data)=>{
                console.log("123"+data);

            });

    },
    updated() {
        this.fetchCustomers();
    },
    methods:{
        fetchCustomers(){
            this.$http.get("http://localhost:3000/users")
            .then((data)=>{
                this.customers=data.body;
            })
            if(this.$route.query.alert){
                this.AlertData=this.$route.query.alert;
            }

        }
    }
}
</script>
